  transform: rotate(-90deg);
  position: absolute;
  top:99px;
  left:668px;
} */
.percentage{
  position:absolute;
  transform: translateY(80px);
  font: normal normal 600 16px/19px Public Sans;
letter-spacing: 0px;
color: #10AC84;
opacity: 1;
}

.input-modify{

    padding-bottom: 8px ;
    outline: 0;
    border-width: 0 0 2px;
    border-color: #10AC84;

}
.invalid{
  border-color:#FF0000;
  color: #FF0000;
}

